.myPage, .myPage div, .myPage span{
    font-family: 'MapoPeacefull';
}

.myPage{
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 100rem;
}

.myPColor{
   background-color: #CAFFBE;
    width: 20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.myPTitle{
    margin-top: 2rem;
    font-weight: bold;
    font-size: x-large;
}

.myPUser{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 2rem;
    width: 17rem;
    height: 9rem;
    border-radius: 30px;
    background-color: white;
    padding: 1.5rem 1rem 1.5rem 1rem;
    text-align: center;
}

.myPUserText{
    font-size: x-large;
}

.myPContent{
    border: 1px solid black;
    width: 50rem;
}

.myPAlertBox{
    display: flex;
    flex-direction: row;
    align-items: center;
    /*위 오른쪽 아래 왼쪽*/
    margin: 1rem 0 0 1rem;
}

.myPIcon{
    height: 2rem !important;
    width: 1.5rem !important;
    margin-right: 0.2rem;
}

/* 경기 전적 */
.matchHistory, .fairPlay{
    margin: 2rem 0 0 1rem;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.matchHistoryBox{
    display: flex;
    flex-wrap: wrap;
}

/* 경기 전적 map함수 */
.gameItem{
    width: 8rem;
    height: 6rem;
    box-shadow: 6px 6px 5px #bdbaba;
    margin-right: 5rem;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border: 1px solid #d5d4d4;
}

.gameName{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /*justify-content: space-around;*/
    margin-left: 0.5rem;
}

.matchHistoryName{
    font-weight: bold;
}

.gameIcon{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*width: 100% !important;*/
    /*height: 100% !important*/
}

.matchRecord span{
    font-size: smaller;
}

